import React, { useState } from 'react';
import {
  ActionButton, Dialog, DialogType, DialogFooter, PrimaryButton,
} from 'office-ui-fabric-react';
import useI18n from '../../I18n';

import $ from './style.scss';
import D from './en.d.yml';

/* eslint-disable no-console */
const CaptureMenu: React.SFC = () => {
  const { t } = useI18n(D);
  const [hideDialog, setHideDialog] = useState(true);

  /* Cookie Info */
  // Info Dialog Hide
  const closeDialog = () => {
    setHideDialog(true);
  };
  // Info Dialog Show
  const openDialog = () => {
    setHideDialog(false);
  };

  return (
    <>
      <ActionButton onClick={openDialog}>{t(' ')}</ActionButton>
      <Dialog
        hidden={hideDialog}
        dialogContentProps={{
          type: DialogType.normal,
          title: t('  '),
          showCloseButton: false,
        }}
        className={$.cookieDialog}
      >
        <p>
          {t('           .')}
        </p>
        <p>
          {t('     ,      ,      60 .     ,    .')}
        </p>
        <p>
          {t('Microsoft Internet Explorer          ,            .')}
        </p>
        <p>
          {t('               .')}
        </p>
        <DialogFooter>
          <PrimaryButton onClick={closeDialog} text={t('')} />
        </DialogFooter>
      </Dialog>
    </>
  );
};

export default CaptureMenu;
